import { createClient } from '@supabase/supabase-js';

// Supabase client setup
const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL,
  process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY
);

export default async function WebsitesPage() {
  const { data: websites, error } = await supabase
    .from('web_sites')
    .select('*')
    .order('created_at', { ascending: false });

  if (error) {
    return (
      <div className="p-6 text-red-600 font-semibold">
        Error loading websites: {error.message}
      </div>
    );
  }

  return (
    <div className="max-w-6xl mx-auto p-6">
      <h1 className="text-2xl font-bold mb-6">Guest Posting Websites</h1>
      <div className="overflow-x-auto">
        <table className="min-w-full text-sm border rounded shadow-md">
          <thead className="bg-gray-100">
            <tr>
              <th className="px-4 py-2 text-left">Link</th>
              <th className="px-4 py-2">Badge</th>
              <th className="px-4 py-2">Categories</th>
              <th className="px-4 py-2">Price</th>
              <th className="px-4 py-2">Traffic</th>
              <th className="px-4 py-2">DA / AS / DR</th>
              <th className="px-4 py-2">TAT</th>
              <th className="px-4 py-2">Link Type</th>
            </tr>
          </thead>
          <tbody>
            {websites.map((site) => (
              <tr key={site.id} className="border-t">
                <td className="px-4 py-2 text-blue-600">
                  <a href={site.link} target="_blank" rel="noopener noreferrer">
                    {site.link}
                  </a>
                </td>
                <td className="px-4 py-2">{site.badge}</td>
                <td className="px-4 py-2">
                  {[site.category_1, site.category_2, site.category_3]
                    .filter(Boolean)
                    .join(', ')}
                </td>
                <td className="px-4 py-2">
                  ${site.price_from} - ${site.price_to}
                </td>
                <td className="px-4 py-2">{site.similarweb_traffic}</td>
                <td className="px-4 py-2">
                  DA: {site.moz_da} <br />
                  AS: {site.semrush_as} <br />
                  DR: {site.ahrefs_dr_range}
                </td>
                <td className="px-4 py-2">{site.tat}</td>
                <td className="px-4 py-2">{site.link_attribution_type}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}
